@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Montserrat', sans-serif;
  color: rgb(110, 110, 110);
}

.App{
  background-color: rgb(240, 239, 239);
  position: relative;
  min-height: 100vh;
  width: 100vw;
}
.space-div{
  height: 120px;
}


.navbar{
  background-color: rgb(248, 248, 248);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: fixed;
  width: 94vw;
  padding: 5px 50px;
  box-shadow: 5px 5px rgb(235, 234, 234);
  z-index: 10;
}

.navbar h2{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.navbar-group{
  display: flex;
  flex-direction: row;
  align-items: center;
}
.navbar-element{
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 5px;
  margin-left: 20px;
}
.nav-sign{
  background-color: transparent;
  border: 1px solid rgb(110, 110, 110);
  font-size: 20px;
}



.sign-in{
  border: 1px solid rgb(110, 110, 110);
  background-color: rgb(248, 248, 248);
  display: flex;
  flex-direction: column;
  padding: 20px;
  position: absolute;
  top: 300px;
  left: calc(50% - 145px);

}

.sign-in-button{
  border: 1px solid rgb(110, 110, 110);
  background-color: rgb(248, 248, 248);
  padding: 10px;
  width: 250px;
  font-size: 30px;
  margin: 10px 0;
}
.sign-in input{
  background-color: transparent;
  padding: 10px;
  width: 250px;
  font-size: 30px;
}
.signIn-sideBar{
  position: absolute;
  left: -55px;
  top: -55px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.signIn-special{
  border: 1px solid rgb(110, 110, 110);
  font-size: 25px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 5px 13px;
  border-radius: 50%;
}






.page-home{
  padding: 60px;
  display: flex;
  flex-direction: column;
}


.levelBar {
  display: flex;
  flex-direction: column;
}

.levelBar-title {
  font-size: 45px;
}

.levelBar-images{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.levelBar-images img{
  height: 300px;
  margin: 10px 15px;
}


.game-image{
  display: block;
  width: 90%;
  margin: 0 auto;
}

.dropdown{
  display: flex;
  flex-direction: column;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: absolute;
}
.dropdown div{
  background-color: rgb(248, 248, 248);
  border: 1px solid rgb(110, 110, 110);
  padding: 10px 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.dropdown h3{
  margin-left: 15px;
}

.dropdown img{
  width: 45px;
  display: inline;
}

.timer{
  font-size: 50px;
  width: 130px;
  position: fixed;
  left: calc(50% - 65px);
  top: 5px;
  z-index: 12;
}
.page-leaderboard{
  padding: 50px;
}

.leaderboard-types button{
  border: 1px solid rgb(110, 110, 110);
  color: rgb(110, 110, 110);
  font-size: 20px;
  padding: 5px 20px;
  border-radius: 10%;
  margin: 0 5px;
}

.leaderboard-levels{
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.leaderboard-levels img{
  height: 200px;
}

.leaderboard-board{
  width: 80%;
  display: block;
  margin: 20px auto;
}
.leaderboard-board h2{
  font-size: 45px;
}
.leaderboard-board table{
  width: 100%;
  border-spacing: 12px;
}

.leaderboard-board th{
  font-size: 30px;
}
.leaderboard-board td{
  font-size: 25px;
}
.leaderboad-left{
  float: left;
}
.leaderboad-right{
  float: right;
}

